<template>
  <div class="content_box">
      <app-table-utils :utils="utils">
          <el-input v-model="queryData.name" slot="query-1"/>
          <el-input disabled v-model="queryData.group" slot="query-2"/>
          <el-button slot="query" class="queryBtn" >查询</el-button>
        </app-table-utils>
        <div class="table-line"></div>
        <app-tables :table="table"  @pageChange="handleCurrentChange">
          <template slot="column" slot-scope="{data}">
            <!-- <template v-if="data.col.key === 'status'">
              {{data.row[data.col.key] == '0' ? '草稿' : '发布' || '-'}}
            </template> -->
            <template>{{data.row[data.col.key] || '-'}}</template>
          </template>
        </app-tables>
  </div>
</template>
<script>
export default {
  name:'SystemLog',
  data(){
    return {
        utils: { //表格工具栏
              left: [
                  { label: '设备名称', slot: 'query-1' },
                  { label: '归属分组', slot: 'query-2' },
                  { slot: 'query' },
              ],
              cols: [24]
          },
        queryData:{
          pageNum:1,
          pageSize:10,
          name:null,
          group: '下拉框'
        },
        table: {
              data: [], //表格数据
              total: 0, //总页数
              tableType: 1, //表格类型
              cols: [ //表格列配置
                  {
                      key: '序号',
                      label: '序号'
                  },
                  {
                      key: '设备名称',
                      label: '设备名称'
                  },
                  {
                      key: '设备ID',
                      label: '设备ID'
                  },
                  {
                      key: '设备型号',
                      label: '设备型号'
                  },
                   {
                      key: '实际位置',
                      label: '实际位置'
                  },
                   {
                      key: '所属分组',
                      label: '所属分组'
                  },
                   {
                      key: '备注',
                      label: '备注'
                  },
                   {
                      key: '时间',
                      label: '时间'
                  },
                   {
                      key: '用户',
                      label: '用户'
                  }
              ]
          }
    }
  },
  created(){
    this.init();
  },
  methods:{
        /**
     * 初始化数据
     */
    init(){
      this.table.data = [
        {
          '序号':1,
          '设备名称': '设备一',
          '设备ID': 'V102',
          '设备型号': 'VSIN01',
          '实际位置': '四川环球广场',
          '所属分组': '四川',
          '备注': '测试专用',
          '时间': '2019-09-26 09:30:30',
          '用户': 'admin'
        }
      ]
    },

     /**
       * 分页器回调
       */
      handleCurrentChange(page) {
          this.queryData.pageNum = page;
          this.init();
      },
  }
}
</script>





